<template>
  <div class="flex flex-col h-screen max-h-screen">
    <!-- search -->
    <div class="holar z-20 flex justify-center relative px-4 pt-8 pb-32 bg-hero-pattern bg-cover">
      <div class="w-full max-w-screen-sm">
        <h2 class="text-white text-center text-xl">IP ADDRESS TRACKER</h2>
        <div class="flex mt-4">
          <input 
          v-model="ipsearch"
          type="text"
          class="flex-1 rounded-tl-md rounded-bl-md p-3 focus:outline-none"
          placeholder="Search IP address or leave empty to get your ip info"
          autofocus>
          <i @click="getIpInfo" class="rounded-br-md rounded-tr-md flex items-center px-4 bg-indigo-50 cursor-pointer fas fa-chevron-right"></i>
        </div>
      </div>
      <IPinfo v-if="ipInfo" :finalresult="ipInfo"/>
    </div>

    <!-- map -->
    <div id="map" class="h-full z-10">
    </div>
  </div>
</template>

<script>

import IPinfo from '../components/IPinfo.vue';
import leaflet from 'leaflet';
import { onMounted, ref } from '@vue/runtime-core';
export default {
  name: 'Home',
  components: {IPinfo},
  setup() {
    let map;
    const ipsearch = ref('');
    const ipInfo = ref(null);
    onMounted(() => {
      map = leaflet.map('map').setView([51.505, -0.09], 13);
      leaflet.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoiaGFuY2hvdGhhciIsImEiOiJja3g0OHRvaDUyMGpxMnJ0OW5iem5kMDM2In0.7PHnTYtzVGcsu3BTXearTg', {
          attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, Imagery Â© <a href="https://www.mapbox.com/">Mapbox</a>',
          maxZoom: 18,
          id: 'mapbox/streets-v11',
          tileSize: 512,
          zoomOffset: -1,
          accessToken: 'pk.eyJ1IjoiaGFuY2hvdGhhciIsImEiOiJja3g0OHRvaDUyMGpxMnJ0OW5iem5kMDM2In0.7PHnTYtzVGcsu3BTXearTg'
      }).addTo(map);
    })

    const getIpInfo = async() => {
      try{
        let getData = await fetch(`https://geo.ipify.org/api/v2/country,city?apiKey=at_fUOqRyFGrDQ3f2SGVYkMfxHDNZ4uj&ipAddress=${ipsearch.value}`)
        let resData = await getData.json()
        ipInfo.value = {
          ipaddress: resData.ip,
          location: resData.location.region,
          timezone: resData.location.timezone,
          isp: resData.isp,
          lat: resData.location.lat,
          lng: resData.location.lng
        }
        leaflet.marker([ipInfo.value.lat, ipInfo.value.lng]).addTo(map);
        map.setView([ipInfo.value.lat, ipInfo.value.lng], 13);
        ipsearch.value = ''
      }
      catch(err){
        alert(err.message)
      }
    }

    return {
      ipsearch,
      ipInfo,
      getIpInfo
    }
  }
}
</script>
